.item-card {
    box-shadow: rgb(0,0,0,0.34) 1px 1px 5px;
    min-height: 275px;
    width: 100%;
}


.add-to-cart:hover {
    background-color: #BB9771;
    color: #002255;
}

.remove-from-cart {
    background: none;
    color: #f8c54f;
    font-size: 14px;
    cursor: pointer;
    outline: none;
  }
  
  .remove-from-cart:hover {
    color: #BEB3AC;
  }
  